import React from 'react';
import './CommonPageItems.css';
import Preview from '../../components/Preview/Preview';
import Description from '../../components/Description/Description';
import data from '../../data/projects';

function CommonPageItems(props) {  
  return (
    data.map((item, index) => {
      return (
        <div className="common-page"
             key={index}>          
          <Preview data={item}/>
          <div className="description-container">
            <Description data={item}/>
            <div className="description-btn-container">
              <div className="btn-cont">
                <a className="btn" href={item.link1} target="_blank" rel="noopener noreferrer">
                  Demo
                  <span className="line-1"></span>
                  <span className="line-2"></span>
                  <span className="line-3"></span>
                  <span className="line-4"></span>
                </a>
              </div>
              <div className="btn-cont">
                <a className="btn" href={item.link3} target="_blank" rel="noopener noreferrer">                  
                  Github
                  <span className="line-1"></span>
                  <span className="line-2"></span>
                  <span className="line-3"></span>
                  <span className="line-4"></span>
                </a>
              </div>
            </div>
          </div>
        </div>
      );
    })
  )
}

export default CommonPageItems;